<template>
  <div>
    <v-row v-if="headersWithDataType.length">
      <v-col cols="12" sm="6">
        <div ref="topelement"></div>
        <label class="as">{{ $t("detected_as") }}</label>
      </v-col>
      <v-col cols="12" sm="6">
        <label class="as">{{ $t("imported_as") }}</label>
      </v-col>
    </v-row>
    <div
      v-for="headerWithDataType in headersWithDataType"
      :key="headerWithDataType.header"
      class="loop-row"
    >
      <v-col cols="12" sm="6">
        <input
          type="text"
          class="detected-as-input"
          :value="headerWithDataType.header"
          disabled
        />
      </v-col>
      <v-col cols="12" sm="6">
        <select
          :value="headerWithDataType.dataType"
          class="imported-as-drop-down"
          :class="{
            ignored: headerWithDataType.dataType === 'ignored'
          }"
          @change.prevent="changeDataType($event, headerWithDataType.header)"
        >
          <option
            v-for="dataType in dataTypes"
            :key="dataType.value"
            :value="dataType.value"
          >
            {{ dataType.text }}
          </option>
        </select>
      </v-col>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    headersWithDataType: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data() {
    return {
      dataTypes: [
        { text: "Date * ", value: "date" },
        { text: "Amount * ", value: "currency_amount" },
        { text: "Deposit Amount", value: "deposit_amount" },
        { text: "Withdrawal Amount", value: "withdrawal_amount" },
        { text: "Type * ", value: "type" },
        { text: "Asset", value: "asset" },
        { text: "Category", value: "category" },
        { text: "Description * ", value: "description" },
        { text: "Fee", value: "fee" },
        { text: "Institution", value: "institution" },
        { text: "Investment", value: "investment" },
        { text: "Note", value: "note" },
        { text: "Quantity", value: "quantity" },
        { text: "Gain", value: "realized_gain" },
        { text: "Security", value: "security" },
        { text: "Settlement Date", value: "settlement_date" },
        { text: "Symbol", value: "symbol" },
        { text: "Ignored", value: "ignored" }
      ]
    };
  },
  methods: {
    async changeDataType(event, header) {
      const dataType = event.target.value;
      this.$emit("changeDataType", { dataType, header });
    }
  }
};
</script>
