import moment from "moment";

const isDate = value => {
  return new Date(value) !== "Invalid Date" && !Number.isNaN(new Date(value));
};
const isNumeric = value => {
  if (typeof value !== "string") return false;
  return !Number.isNaN(value) && !Number.isNaN(parseFloat(value));
};

const isString = value => {
  return typeof value === "string";
};

const lineToObject = (header, line) => {
  const bits = line.split(/,(?=(?:[^"]*"[^"]*")*[^"]*$)/g);
  const obj = {};
  header.forEach((h, i) => {
    obj[h] = bits[i];
  }); // or use reduce here
  return obj;
};
const checkFirstRowIsHeader = firstRow => {
  const headers = firstRow.split(/,(?=(?:[^"]*"[^"]*")*[^"]*$)/g);
  // check if columns are not string or empty;
  const nonStringColumn = headers.filter(header => {
    return typeof header !== "string";
  });
  if (nonStringColumn.length) return false;
  const dateColumn = headers.filter(header => {
    const value =
      Number.isNaN(Number(header)) &&
      new Date(header) !== "Invalid Date" &&
      !Number.isNaN(Number(new Date(header))) &&
      moment(new Date(header)).isValid();
    return value;
  });
  if (dateColumn.length) return false;
  return true;
};
const originalCSV = (csv, manualHeaderSelection, csvContainsHeader) => {
  let lines = csv.split("\n");
  // remove blank lines
  lines = lines.filter(
    line => line && line !== "\n" && line !== "\r" && line !== "\r\n"
  );
  const firstRow = lines[0];
  let isFirstRowHeader = null;
  if (manualHeaderSelection) {
    isFirstRowHeader = csvContainsHeader;
  } else {
    isFirstRowHeader = checkFirstRowIsHeader(firstRow);
  }

  let header = "";
  // get rid of definitions
  if (!isFirstRowHeader) {
    // push Col1 , Col2 etc.
    const firstRowData = firstRow.split(/,(?=(?:[^"]*"[^"]*")*[^"]*$)/g);

    firstRowData.forEach((h, i) => {
      header = `${header}Col${i + 1}${
        i === firstRowData.length - 1 ? "" : ","
      }`;
    });
    lines[0] = header;
  }
  return lines.map(line => {
    return line.split(/,(?=(?:[^"]*"[^"]*")*[^"]*$)/g);
  });
};

const parseCSV = (csv, manualHeaderSelection, csvContainsHeader) => {
  let lines = csv.split("\n");
  // remove blank lines
  lines = lines.filter(
    line => line && line !== "\n" && line !== "\r" && line !== "\r\n"
  );
  const firstRow = lines[0];
  let isFirstRowHeader = null;
  if (manualHeaderSelection) {
    isFirstRowHeader = csvContainsHeader;
  } else {
    isFirstRowHeader = checkFirstRowIsHeader(firstRow);
  }
  let header = [];
  // get rid of definitions
  if (!isFirstRowHeader) {
    // push Col1 , Col2 etc.
    const firstRowData = firstRow.split(/,(?=(?:[^"]*"[^"]*")*[^"]*$)/g);
    firstRowData.forEach((h, i) => header.push(`Col${i + 1}`));
  } else {
    lines.shift();
    header = firstRow.split(/,(?=(?:[^"]*"[^"]*")*[^"]*$)/g);
  }
  return lines.map(line => lineToObject(header, line));
};

const isDataTypeCorrect = (value, dataType) => {
  switch (dataType) {
    case "date-time":
      return isDate(value);
    case "number":
      return isNumeric(value);
    case "string":
      return isString(value);
    default:
      return false;
  }
};

const findDataType = (key, value) => {
  const dataTypes = [
    {
      name: "date",
      type: "date-time",
      matching_words: ["date", "transaction date"]
    },
    {
      name: "currency_amount",
      type: "number",
      matching_words: ["Amount", "Currency Amount", "Total"]
    },
    {
      name: "deposit_amount",
      type: "number",
      matching_words: ["Amount", "Currency Amount", "Total"]
    },
    {
      name: "withdrawal_amount",
      type: "number",
      matching_words: ["Amount", "Currency Amount", "Total"]
    },
    { name: "type", type: "string", matching_words: ["Type", "Activity"] },
    { name: "asset", type: "String", matching_words: ["asset"] },
    { name: "category", type: "string", matching_words: ["category"] },
    {
      name: "description",
      type: "string",
      matching_words: ["description"]
    },
    { name: "fee", type: "number", matching_words: ["fee"] },
    {
      name: "institution",
      type: "string",
      matching_words: ["institution"]
    },
    { name: "investment", type: "string", matching_words: ["investment"] },
    { name: "note", type: "string", matching_words: ["note"] },
    { name: "quantity", type: "number", matching_words: ["quantity"] },
    {
      name: "realized_gain",
      type: "number",
      matching_words: ["realized_gain", "gain"]
    },
    { name: "security", type: "string", matching_words: ["security"] },
    {
      name: "settlement_date",
      type: "date-time",
      matching_words: ["Settlement Date"]
    },
    { name: "symbol", type: "string", matching_words: ["symbol"] }
  ];
  const matchingDataTypes = dataTypes.filter(
    dataType =>
      dataType.matching_words.some(
        v => key.toLowerCase() === v.toLowerCase()
      ) && isDataTypeCorrect(value, dataType.type)
  );
  const [foundDatType] = matchingDataTypes;
  if (foundDatType) {
    return { header: key, dataType: foundDatType.name };
  }
  return { header: key, dataType: "ignored" };
};

const isKeyIgnored = (headersWithDataType, header) => {
  const ignoredArray = headersWithDataType
    .filter(headerDataType => headerDataType.dataType !== "ignored")
    .map(headerDataType => {
      return headerDataType.header;
    });
  const isIgnored = !ignoredArray.includes(header);
  return isIgnored;
};

const parseValue = (dataType, _value) => {
  let value = _value;
  switch (dataType) {
    case "currency_amount": {
      if (!value) return null;
      const matches = value.match(/((\d){1,3})+([,][\d]{3})*([.](\d)*)?/g);
      if (!matches || !matches.length) return null;
      let [firstMatch] = value;
      firstMatch = firstMatch.replace(/,/g, "");
      return firstMatch;
    }

    case "deposit_amount": {
      if (!value) return null;
      const matches = value.match(/((\d){1,3})+([,][\d]{3})*([.](\d)*)?/g);
      if (!matches || !matches.length) return null;
      let [firstMatch] = value;
      firstMatch = firstMatch.replace(/,/g, "");
      return firstMatch;
    }

    case "withdrawal_amount": {
      if (!value) return null;
      const matches = value.match(/((\d){1,3})+([,][\d]{3})*([.](\d)*)?/g);
      if (!matches || !matches.length) return null;
      let [firstMatch] = matches;
      firstMatch = firstMatch.replace(/,/g, "");
      return firstMatch;
    }

    case "date":
      value = moment(value).format("YYYY-MM-DDTHH:mm:ss");
      return value;
    case "type":
      value = value.toLowerCase();
      return value;
    default:
      return value;
  }
};

const getSelectedDataTypeAndValue = (headersWithDataType, dataType, _value) => {
  const headerObject = headersWithDataType.filter(
    headerDataType => headerDataType.header === dataType
  );
  const value = parseValue(headerObject[0].dataType, _value);
  return { dataType: headerObject[0].dataType, newValue: value };
};

export {
  findDataType,
  isKeyIgnored,
  parseCSV,
  originalCSV,
  getSelectedDataTypeAndValue
};
