<template>
  <div>
    <form enctype="multipart/form-data">
      <select-type @updateImportTo="updateImportTo" />

      <v-row>
        <v-col cols="24" sm="12">
          <div class="row-heading">
            <template v-if="importTo === 'institution'">
              {{ $t("select_institution") }}
            </template>
            <template v-else> {{ $t("select_asset") }} </template>
          </div>
        </v-col>
        <v-col cols="24" sm="12" class="select-institution-sec">
          <select-institution
            v-if="importTo === 'institution'"
            ref="selectInstitution"
            @updateInstiutionId="updateInstiutionId"
            @updateInvestmentId="updateInvestmentId"
          />
          <select-asset
            v-if="importTo === 'asset'"
            ref="selectAsset"
            @updateAssetId="updateAssetId"
          />
        </v-col>
      </v-row>

      <upload @handleFileUpload="handleFileUpload" />
      <v-row>
        <v-col cols="24" sm="12">
          <div class="row-heading">{{ $t("identify_columns") }}</div>
        </v-col>
        <v-col cols="24" sm="12">
          <div class="account-currency">{{ $t("account_currency") }}</div>
          {{ $t("selected_currency") }}
          <span class="selected-currency">{{ selectedCurrency._id }}</span>
          {{ $t("transactions_imported_as") }}
          <span class="selected-currency">{{ selectedCurrency._id }}</span>
        </v-col>
        <v-col cols="12" sm="6">
          <select-data-type
            ref="selectDataType"
            :headers-with-data-type="headersWithDataType"
            @changeDataType="changeDataType"
          />
        </v-col>
        <v-col cols="12" sm="6">
          <guidelines />
        </v-col>
      </v-row>

      <v-row v-if="headersWithDataType.length">
        <v-col cols="24" sm="12">
          <div class="row-heading">
            {{ $t("review_data") }}
          </div>
        </v-col>
        <v-col cols="24" sm="12" align="center" justify="center">
          <v-btn depressed color="primary" @click="uploadCSV">{{
            $t("import")
          }}</v-btn>
        </v-col>
        <v-col cols="24" sm="12">
          <data-table
            :original-data-arr="originalDataArr"
            :accepted-headers="acceptedHeaders"
            :selected-transaction-columns="selectedTransactionColumns"
          />
        </v-col>
      </v-row>
    </form>
    <div class="text-center">
      <v-dialog v-model="dialog" persistent width="300">
        <v-card color="primary" dark>
          <v-card-text>
            Saving your transactions! Please wait!
            <v-progress-linear
              indeterminate
              color="white"
              class="mb-0"
            ></v-progress-linear>
          </v-card-text>
        </v-card>
      </v-dialog>
      <v-dialog v-model="errorDialog" width="600">
        <v-card>
          <v-card-title class="headline grey lighten-2" primary-title>
            Failed Transactions
          </v-card-title>

          <v-card-text>
            <div
              v-for="(failedTransaction, key) in failedTransactions"
              :key="key"
            >
              {{ failedTransaction.error.message }}
            </div>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>

            <v-btn
              v-if="showGoToSavedTransactions"
              color="green darken-1"
              text
              @click="goToSavedTransactions"
            >
              Saved Transactions
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import moment from "moment";
import { required } from "vuelidate/lib/validators";
import {
  isKeyIgnored,
  findDataType,
  parseCSV,
  originalCSV,
  getSelectedDataTypeAndValue
} from "../utils/util";
import Guidelines from "./Guidelines.vue";
import DataTable from "./DataTable.vue";
import SelectType from "./SelectType.vue";
import SelectInstitution from "./SelectInstitution.vue";
import SelectAsset from "./SelectAsset.vue";
import Upload from "./Upload.vue";
import SelectDataType from "./SelectDataType.vue";

export default {
  name: "UploadForm",
  provide() {
    return {
      $v: this.$v
    };
  },
  components: {
    Guidelines,
    DataTable,
    SelectInstitution,
    SelectType,
    SelectAsset,
    Upload,
    SelectDataType
  },
  data() {
    return {
      importTo: "institution",
      account: null,
      institutionId: "",
      investmentId: "",
      assetId: "",
      file: null,
      headers: [],
      headersWithDataType: [],
      dataArr: [],
      originalDataArr: [],
      dialog: false,
      errorDialog: false,
      failedTransactions: [],
      redirectURL: "#",
      showGoToSavedTransactions: false
    };
  },
  validations: {
    institutionId: {
      required
    },
    investmentId: {
      required
    },
    assetId: {
      required
    }
  },
  computed: {
    ...mapGetters([
      "institutions",
      "assets",
      "selectedCurrency",
      "savedTransactions"
    ]),
    selectedTransactionColumns() {
      return this.dataArr.map(data => {
        const newObj = {};
        Object.entries(data).forEach(entry => {
          const [key, value] = entry;
          if (!isKeyIgnored(this.headersWithDataType, key)) {
            newObj[key] = value;
          }
        });
        return newObj;
      });
    },
    importData() {
      return this.selectedTransactionColumns.map(data => {
        const newObj = {};
        Object.entries(data).forEach(entry => {
          const [key, value] = entry;
          const { dataType, newValue } = getSelectedDataTypeAndValue(
            this.acceptedHeaders,
            key,
            value
          );
          if (dataType === "deposit_amount" && newValue) {
            newObj.type = "deposit";
            newObj.currency_amount = newValue;
          } else if (dataType === "deposit_amount" && !newValue) {
            // do nothing
          } else if (dataType === "withdrawal_amount" && newValue) {
            newObj.type = "withdrawal";
            newObj.currency_amount = newValue;
          } else if (dataType === "withdrawal_amount" && !newValue) {
            // do nothing
          } else {
            newObj[dataType] = newValue;
          }
        });
        if (newObj.type === "withdrawal") {
          newObj.currency_amount = -newObj.currency_amount;
        }
        return newObj;
      });
    },
    fromDate() {
      const mnDate = this.importData.reduce(function(a, b) {
        return moment(b.date).diff(moment(a.date)) >= 0 ? a : b;
      });
      return moment(mnDate.date).format("YYYY-MM-DD");
    },
    toDate() {
      const mnDate = this.importData.reduce(function(a, b) {
        return moment(b.date).diff(moment(a.date)) >= 0 ? b : a;
      });
      return moment(mnDate.date).format("YYYY-MM-DD");
    },

    acceptedHeaders() {
      return this.headersWithDataType.filter(
        item => item.dataType !== "ignored"
      );
    }
  },

  methods: {
    ...mapActions(["saveTransactions"]),
    async handleFileUpload(payload) {
      const data = await this.getFileData(payload.file);
      this.originalDataArr = originalCSV(
        data,
        payload.manualHeaderSelection,
        payload.csvContainsHeader
      );
      this.dataArr = parseCSV(
        data,
        payload.manualHeaderSelection,
        payload.csvContainsHeader
      );
      const headers = this.dataArr[0];
      const headersWithDataType = [];
      Object.entries(headers).forEach(([key, value]) => {
        const headerWithDataType = findDataType(key, value);
        headersWithDataType.push(headerWithDataType);
      });
      this.headersWithDataType = headersWithDataType;
    },
    async getFileData(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(atob(reader.result.split(",")[1]));
        reader.onerror = error => reject(error);
      });
    },
    async changeDataType({ dataType, header }) {
      this.headersWithDataType = this.headersWithDataType.map(
        headerDataType => {
          if (headerDataType.header === header) {
            return { header, dataType };
          }
          return { ...headerDataType };
        }
      );
    },
    async validateInput() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        if (this.importTo === "institution") {
          if (this.$v.institutionId.$error) {
            const element = this.$refs.selectInstitution.$refs.institutionId;
            element.scrollIntoView({ behavior: "smooth" });
            return false;
          }
          if (this.$v.investmentId.$error) {
            const element = this.$refs.selectInstitution.$refs.investmentId;
            element.scrollIntoView({ behavior: "smooth" });
            return false;
          }
        } else if (this.$v.assetId.$error) {
          const element = this.$refs.selectAsset.$refs.assetId;
          element.scrollIntoView({ behavior: "smooth" });
          return false;
        }
      }
      return true;
    },

    async validateDataType() {
      const topElement = this.$refs.selectDataType.$refs.topelement;
      topElement.scrollIntoView({ behavior: "smooth" });

      const selectedHeaders = this.acceptedHeaders.filter(element => {
        return (
          element.dataType === "date" || element.dataType === "description"
        );
      });
      const amountHeader = this.acceptedHeaders.filter(element => {
        return element.dataType === "currency_amount";
      });
      const depositWithdrawalHeader = this.acceptedHeaders.filter(element => {
        return (
          element.dataType === "deposit_amount" ||
          element.dataType === "withdrawal_amount"
        );
      });
      const typeHeader = this.acceptedHeaders.filter(element => {
        return element.dataType === "type";
      });
      if (!amountHeader.length && !depositWithdrawalHeader.length) {
        alert(
          "Either currency_amount or deposit_amount , withdrawal_amount is mandatory"
        );
        return false;
      }
      if (amountHeader.length && depositWithdrawalHeader.length) {
        alert(
          "You can choose either type, amount or depoist_amount,withdrawal_amount"
        );
        return false;
      }
      if (amountHeader.length && !typeHeader.length) {
        alert("If you choose amount then type is required to select.");
        return false;
      }
      if (amountHeader.length && typeHeader.length) {
        if (selectedHeaders.length < 2) {
          alert(
            "Please make sure you fill all mandatory fields currency_amount, date,  type,  description are required."
          );
          return false;
        }
      } else if (depositWithdrawalHeader.length > 0) {
        if (selectedHeaders.length < 2) {
          alert(
            "Please make sure you fill all mandatory fields deposit_amount or withdrawal_amount, date, description are required."
          );
          return false;
        }
      }
      return true;
    },

    async uploadCSV() {
      const isInputValidated = await this.validateInput();
      if (!isInputValidated) {
        return;
      }
      const isDataTypeValidated = await this.validateDataType();
      if (!isDataTypeValidated) {
        return;
      }

      this.dialog = true;

      const payload = {
        importTo: this.importTo,
        institutionId: this.institutionId,
        investmentId: this.investmentId,
        assetId: this.assetId,
        importData: this.importData
      };

      await this.saveTransactions(payload);
      if (this.savedTransactions && this.savedTransactions.length) {
        this.failedTransactions = this.savedTransactions.filter(
          transaction => transaction.error
        );
        if (this.savedTransactions.length !== this.failedTransactions.length) {
          this.showGoToSavedTransactions = true;
        }
        let redirectURL = "";
        if (this.importTo === "institution") {
          redirectURL = `institutions=${this.institutionId}`;
        } else {
          redirectURL = `assets=${this.assetId}`;
        }
        this.redirectURL = `https://app.wealthica.com/transactions?peek=true&${redirectURL}&from=${this.fromDate}&to=${this.toDate}`;
        this.dialog = false;
        if (this.failedTransactions.length) {
          this.errorDialog = true;
        } else {
          setTimeout(() => {
            window.top.location.href = this.redirectURL;
          }, 200);
        }
      }
    },
    goToSavedTransactions() {
      window.top.location.href = this.redirectURL;
    },

    updateImportTo(value) {
      this.importTo = value;
    },
    updateInstiutionId(value) {
      this.institutionId = value;
    },
    updateInvestmentId(value) {
      this.investmentId = value;
    },
    updateAssetId(value) {
      this.assetId = value;
    }
  }
};
</script>

