<template>
  <v-row>
    <v-col cols="24" sm="12">
      <div class="row-heading">
        {{ $t("upload_csv") }}
      </div>
    </v-col>
    <v-col cols="24" sm="12">
      <div class="upload-file-wrapper">
        <div class="upload-file-left">
          <input id="file" ref="file" type="file" @change="handleFileUpload" />
        </div>
        <div class="upload-file-middle">
          <v-switch
            v-model="manualHeaderSelection"
            label="Manual Header Selection"
          ></v-switch>
        </div>
        <div class="upload-file-right">
          <v-switch
            v-model="csvContainsHeader"
            label="CSV contains header"
          ></v-switch>
        </div>
      </div>
    </v-col>
  </v-row>
</template>

<script>
export default {
  data() {
    return {
      csvContainsHeader: false,
      manualHeaderSelection: false
    };
  },
  methods: {
    handleFileUpload(event) {
      event.preventDefault();
      const files = event.target.files || event.dataTransfer.files;
      const file = files[0];
      this.$emit("handleFileUpload", {
        file,
        csvContainsHeader: this.csvContainsHeader,
        manualHeaderSelection: this.manualHeaderSelection
      });
    }
  }
};
</script>
